import { ReactNode } from 'react';
import Navbar from '@/components/navbar/Navbar';
import LeftSidebar from '@/components/sidebar/LeftSidebar';
import RightSidebar from '@/components/sidebar/RightSidebar';

interface LayoutProps {
  children: ReactNode;
}

export default function Layout({ children }: LayoutProps) {
  return (
    <div className="min-h-screen flex flex-col">
      {/* Fixed navbar at the top */}
      <Navbar className="fixed top-0 left-0 right-0 z-50" />
      
      {/* Main content area with sidebars */}
      <div className="flex-1 flex pt-16"> {/* pt-16 to account for fixed navbar */}
        {/* Left sidebar */}
        <LeftSidebar className="w-64 fixed left-0 top-16 bottom-0 overflow-y-auto" />
        
        {/* Main content */}
        <main className="flex-1 ml-64 mr-64 min-h-[calc(100vh-4rem)] overflow-x-hidden">
          <div className="container mx-auto px-4 py-6">
            {children}
          </div>
        </main>
        
        {/* Right sidebar */}
        <RightSidebar className="w-64 fixed right-0 top-16 bottom-0 overflow-y-auto" />
      </div>
    </div>
  );
}
