import { FC } from 'react';

interface LeftSidebarProps {
  className?: string;
}

const LeftSidebar: FC<LeftSidebarProps> = ({ className = '' }) => {
  return (
    <aside className={`bg-gray-50 border-r ${className}`}>
      <div className="p-4">
        {/* Add your left sidebar content here */}
      </div>
    </aside>
  );
};

export default LeftSidebar; 